import React, { Component } from "react";
import Select from "react-select";
import PropTypes from "prop-types";
import style from "./CustomCalc.module.css";
import engines from "../../assets/data/engines.json";
import { carYear } from "../../assets/data/carYear.json";

class CustomCalc extends Component {
  state = {
    portExpedition: 450,
    brokerPrice: 400,
    esv: "",
    evacution: 250,
    certification: 200,
    accounting: 40,
    engineType: "",
    engineToSelect: {},
    yearsToSelect: [],
    carYear: "",
    engineVolume: "",
    importDuty: "",
    exise: "",
    nds: "",
    totalDelivery: ""
  };

  static propTypes = {
    carPrice: PropTypes.string.isRequired,
    aucComission: PropTypes.string.isRequired,
    totalDelivery: PropTypes.string.isRequired
  };

  componentDidMount() {
    this.totalCustomCalc();
    if (engines && carYear) {
      const engineToSelect = [
        ...engines.engines.map(el => {
          return {
            value: el,
            label: el
          };
        })
      ];
      const yearsToSelect = [
        ...carYear.map(el => {
          return {
            value: el,
            label: el
          };
        })
      ];
      this.setState({
        engineToSelect: [...engineToSelect],
        yearsToSelect: [...yearsToSelect]
      });
    }
  }

  componentDidUpdate(prevProps) {
    const { carPrice, aucComission, totalDelivery } = this.props;
    if (this.props !== prevProps) {
      const totalCarPrice = Number(carPrice) + Number(aucComission);
      this.setState({
        importDuty: Math.round(totalCarPrice * 0.1),
        totalDelivery
      });
    }
  }

  handleRadioCheck = e => {
    this.setState(
      {
        engineType: e.target.value
      },
      () => {
        this.taxesCalc();
      }
    );
  };

  handleSelectChange = (e, { name }) => {
    this.setState({ [name]: e.value }, () => this.taxesCalc());
  };

  taxesCalc = () => {
    const { carPrice, aucComission } = this.props;
    const { engineType, carYear, importDuty, engineVolume } = this.state;
    if (carYear.length > 1 && engineVolume.length > 1) {
      let coeficient = 50;
      const ageOfCar = 2020 - Number(carYear);
      let exise = "";
      let totalCustomPrice = "";
      let nds = "";
      switch (engineType) {
        case "GAS":
          if (Number(engineVolume) > 3.5) {
            coeficient = 100;
          }
          exise = Math.round(
            Number(ageOfCar) * Number(engineVolume) * Number(coeficient)
          );
          totalCustomPrice =
            Number(carPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          break;
        case "DIESEL":
          if (Number(engineVolume) > 3.5) {
            coeficient = 150;
          } else coeficient = 75;
          exise = Math.round(
            Number(ageOfCar) * Number(engineVolume) * Number(coeficient)
          );
          totalCustomPrice =
            Number(carPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          break;
        case "HYBRID ENGINE":
          exise = 100;
          totalCustomPrice =
            Number(carPrice) +
            Number(aucComission) +
            Number(exise) +
            Number(importDuty);
          nds = Math.round(totalCustomPrice * 0.2);
          break;
        case "ELECTRIC":
          exise = 0;
          totalCustomPrice = 0;
          nds = 0;
          break;
        default:
          return coeficient;
      }

      const esv = Math.round(totalCustomPrice * 0.04);
      this.setState({ nds, exise, importDuty, esv });
    }
  };

  totalCustomCalc = () => {
    const {
      portExpedition,
      brokerPrice,
      inTax,
      pdv,
      esv,
      evacution,
      certification,
      accounting
    } = this.state;
    // eslint-disable-next-line no-unused-vars
    const totalCustom =
      Number(portExpedition) +
      Number(brokerPrice) +
      Number(inTax) +
      Number(pdv) +
      Number(esv) +
      Number(evacution) +
      Number(certification) +
      Number(accounting);
  };

  render() {
    const {
      portExpedition,
      brokerPrice,
      esv,
      evacution,
      certification,
      accounting,
      exise,
      importDuty,
      nds,
      engineType,
      yearsToSelect,
      engineToSelect,
      totalDelivery
    } = this.state;
    const totalCustom =
      Number(portExpedition) +
      Number(brokerPrice) +
      Number(importDuty) +
      Number(exise) +
      Number(nds) +
      Number(esv) +
      Number(evacution) +
      Number(certification) +
      Number(accounting);
    const totalCarPrice = Number(totalCustom) + Number(totalDelivery);
    console.log(engineToSelect[15]);
    const selectStyles = {
      control: base => ({
        ...base,

        fontSize: "13px;"
      }),
      menu: base => ({
        ...base,
        fontSize: "13px;"
      })
    };
    return (
      <>
        <div className={style.customContainer}>
          <h2 className={style.customTitle}>–ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä —Ä–æ–∑–º–∏—Ç–Ω–µ–Ω–Ω—è</h2>
          <div className={style.engineTypeRadioWrapper}>
            <label htmlFor="BenzineRadio">
              <input
                type="radio"
                id="BenzineRadio"
                checked={engineType === "GAS"}
                value="GAS"
                name="engTypeRadio"
                onChange={this.handleRadioCheck}
              />
              –ë–µ–Ω–∑–∏–Ω
            </label>
            <label htmlFor="DieselRadio">
              <input
                type="radio"
                id="DieselRadio"
                checked={engineType === "DIESEL"}
                value="DIESEL"
                name="engTypeRadio"
                onChange={this.handleRadioCheck}
              />
              –î–∏–∑–µ–ª—å
            </label>
            <label htmlFor="ElectroRadio">
              <input
                type="radio"
                id="ElectroRadio"
                checked={engineType === "ELECTRIC"}
                value="ELECTRIC"
                name="engTypeRadio"
                onChange={this.handleRadioCheck}
              />
              –ï–ª–µ–∫—Ç—Ä–æ
            </label>
            <label htmlFor="HybridRadio">
              <input
                type="radio"
                id="HybridRadio"
                checked={engineType === "HYBRID ENGINE"}
                value="HYBRID ENGINE"
                name="engTypeRadio"
                onChange={this.handleRadioCheck}
              />
              –ì—ñ–±—Ä–∏–¥
            </label>
          </div>
          <div className={style.selectWrapper}>
            <Select
              className={style.selectEngine}
              isSearchable={false}
              styles={selectStyles}
              placeholder="–û–±—î–º –¥–≤–∏–≥—É–Ω–∞"
              options={engineToSelect}
              defaultValue={engineToSelect[15]}
              id="engine"
              name="engineVolume"
              onChange={this.handleSelectChange}
            />

            <Select
              isSearchable={false}
              className={style.carYear}
              styles={selectStyles}
              placeholder="–†—ñ–∫ –≤–∏–ø—É—Å–∫—É –∞–≤—Ç–æ"
              options={yearsToSelect}
              id="carYear"
              name="carYear"
              onChange={this.handleSelectChange}
            />
          </div>

          <span className={style.span}>
            –ï–∫—Å–ø–µ–¥–∏—Ü—ñ—è –≤ –ø–æ—Ä—Ç—É:
            <span className={style.innerSpan}>{portExpedition}$</span>
          </span>
          <br />
          <span className={style.span}>
            –ë—Ä–æ–∫–µ—Ä:<span className={style.innerSpan}>{brokerPrice}$</span>
          </span>
          <br />
          <span className={style.span}>
            –í–≤—ñ–∑–Ω–µ –º–∏—Ç–æ:
            <span className={style.innerSpan}>{importDuty}$</span>
          </span>
          <br />
          <span className={style.span}>
            –ê–∫—Ü–∏–∑–Ω–∏–π –∑–±—ñ—Ä:<span className={style.innerSpan}>{exise}$</span>
          </span>
          <br />
          <span className={style.span}>
            –ü–î–í:<span className={style.innerSpan}>{nds}$</span>
          </span>
          <br />
          <span className={style.span}>
            –Ñ–°–í:<span className={style.innerSpan}>{esv}$</span>
          </span>
          <br />
          <span className={style.span}>
            –ï–≤–∞–∫—É–∞—Ç–æ—Ä –¥–æ –∞–¥—Ä–µ—Å–∏ –∫–ª—ñ—î–Ω—Ç–∞:
            <span className={style.innerSpan}>{evacution}$</span>
          </span>
          <br />
          <span className={style.span}>
            –°–µ—Ä—Ç–∏—Ñ—ñ–∫–∞—Ü—ñ—è:
            <span className={style.innerSpan}>{certification}$</span>
          </span>
          <br />
          <span className={style.span}>
            –ü–æ—Å—Ç–∞–Ω–æ–≤–∫–∞ –Ω–∞ –æ–±–ª—ñ–∫:
            <span className={style.innerSpan}>{accounting}$</span>
          </span>
          <span className={style.totalCustomCost}>
            –°—É–º–∞ –ø–ª–∞—Ç–µ–∂—ñ–≤ –≤ –£–∫—Ä–∞—ó–Ω—ñ:
            <span className={style.innerSpan}> {totalCustom}$</span>
          </span>
          <span className={style.totalCustomCost}>
            –ó–∞–≥–∞–ª—å–Ω–∞ –≤–∞—Ä—Ç—ñ—Å—Ç—å –∞–≤—Ç–æ:
            <span className={style.innerSpan}> {totalCarPrice}$</span>
          </span>
        </div>
      </>
    );
  }
}

export default CustomCalc;
